@import 'bootstrap4.scss';
@import 'variables.scss'; // custom variables
// @import 'reboot.scss'; // custom reboot, forked from bs4
@import 'mixins.scss';
@import 'slick-theme';
// -------------------------------------------------
// should be in @import '_base.scss'
:root {
  @include fluid-type(font-size, 36rem, 90rem, .9rem, 1rem);
  font-family: 'Arial';
}

body {
  // background-color: rgba(255, 244, 255, 0.8);
  font-family: $futura;
}

// a nice hover transition effect
a,
a path {
  transition: color 0.2s ease-in, fill 0.2s ease-in;
  text-decoration: none !important;
}

// -----------------------------------------------
// Screen
// -----------------------------------------------
$screen-small: 576px;
$screen-medium: 768px;
$screen-large: 992px;
$screen-xlarge: 1200px;
$screen-xxlarge: 1440px;
$screen-wide: 1900px;
$screen-max: 2354px;
// bootstrap map
$grid-breakpoints: ( xs: 0, sm: $screen-small, md: $screen-medium, lg: $screen-large, xl: $screen-xlarge, xxl: $screen-xxlarge, wide: $screen-wide, max: $screen-max);
// -------------------------------------------------
// should be in @import '_headings.scss';
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.h7,
.h8,
.h9,
.meta {
  //font-family: $headings-font-family;
  //font-weight: $headings-font-weight;
  //line-height: $headings-line-height;
  //color: $headings-color;
  // color: #333;
  word-break: keep-all;
  letter-spacing: 0;
}

h1,
.h1 {
  @include fluid-type(font-size, $min_width, $max_width, 1.802rem, 5.96rem);
  @include fluid-type(line-height, 20em, 80em, 1em, 1.2em);
}

h2,
.h2 {
  @include fluid-type(font-size, $min_width, $max_width, 1.802rem, 4.768rem);
  @include fluid-type(margin-bottom, $min_width, $max_width, 1rem, 2rem);
  line-height: 1;
}

h3,
.h3 {
  @include fluid-type(font-size, $min_width, $max_width, 1.424rem, 3.052rem);
  @include fluid-type(line-height, 20em, 80em, 1em, 1.2em);
  margin-bottom: vr(1, ms(3));
  margin-top: vr(1, ms(6));
  color: darken($font-color, 20%);
}

h4,
.h4 {
  @include fluid-type(font-size, $min_width, $max_width, 1.266rem, 2.441rem);
  @include fluid-type(line-height, 20em, 80em, 1em, 1.2em);
  font-family: 'Conv_mn-light';
  font-weight: 300;
}

h5,
.h5 {
  @include fluid-type(font-size, $min_width, $max_width, 1.125rem, 2.441rem);
  @include fluid-type(line-height, 20em, 80em, 1em, 1.2em);
}

h6,
.h6 {
  font-size: 1em;
  text-transform: none;
  font-family: 'Conv_mn-book';
}

.main-title {
  h2 {
    @include fluid-type(font-size, $min_width, $max_width, 3.82rem, 5.96rem);
  }
}

// -------------------------------------------------
// should be in @import '_containers.scss';
.section-container {
  margin: 0 auto;
  width: percentage(378 / 480);
  max-width: 192rem;
  @media all and (min-width: 768px) {
    width: percentage(1208 / 1480);
  }
  @media all and (min-width:1440px) {
    width: 1200px;
  }
}

// -------------------------------------------------
// should be in @import '_images.scss';
figure.ar {
  overflow: hidden;
  position: relative;
  margin: 0;
  display: block;
  img {
    @include center-content();
    width: auto;
    filter: grayscale(40%);
  }
}

figure.img-landscape {
  img {
    width: 100%;
    height: auto;
  }
}

figure.img-portrait {
  // height: 100%;
  // width: auto;
  img {
    height: 100%;
    width: auto;
  }
}

figure.square {
  img {
    max-width: 100%;
    height: auto;
  }
}

// Images
[data-aspect-ratio="3:2"] {
  @include aspect-ratio(3, 2);
}

[data-aspect-ratio="2:3"] {
  @include aspect-ratio(2, 3);
}

// -----------------------------------------------
// Remove Padding from bootstrap Columns
// -----------------------------------------------
.row.no-gutters {
  margin-right: 0;
  margin-left: 0;
  &>[class^="col-"],
  &>[class*=" col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}

// -----------------------------------------------
// Outside from framework
// -----------------------------------------------
// Common styles
a {
  color: inherit;
}

a,
button {
  cursor: pointer;
  transition: all 0.2s ease;
}

li {
  list-style-type: none;
}

.bold-text {
  font-weight: 700;
}

.textile-bg {
  background-image: url("../img/clean_textile.jpg");
}

.blue__link {
  padding: 0.8em 2.5em;
  font-family: $eurostile;
  @include rfs(23);
  line-height: 1;
  letter-spacing: 2.3px;
  white-space: nowrap;
  text-align: center;
  color: $white;
  font-weight: 300;
  border-radius: 30px;
  background-color: $cerulean;
  @media only screen and (max-width: 768px) {
    padding: 0.2em 0.6em;
  }
  &:hover {
    background-color: lighten( $cerulean, 3%);
    color: $white;
  }
  border: solid 2px $white;
}

// fluid gallery
.fluid__gallery {
  width: 100%;
  display: flex;
  @media only screen and (max-width: 480px) {
    flex-direction: column;
  }
}

.fluid__gallery-item {
  img {
    width: 100%;
  }
}

.fluid__gallery-item--1 {
  flex-basis: 33.47889374%;
}

.fluid__gallery-item--2 {
  flex-basis: 34.497816593%;
}

.fluid__gallery-item--3 {
  flex-basis: 32.023289665%;
}

// End of common styles
.header {
  background-color: #fff;
  .top-header {
    padding-top: 5px;
    padding-bottom: 5px;
    color: $white;
    background-color: $slate-blue;
    background-image: url("../img/clean_textile_dark.jpg");
    .top-header__caption {
      font-family: $futura;
      @include rfs(17.2);
      font-weight: 700;
      color: inherit;
    }
    .top-header__contact {
      @media only screen and (min-width: 640px) {
        text-align: right;
      }
    }
    span {
      white-space: nowrap;
      a {
        &:hover {
          color: $cerulean;
        }
      }
    }
    .top-header__tel,
    .top-header__schedule {
      span:nth-child(2) {
        font-weight: 700;
      }
    }
    @media only screen and (max-width: 640px) {
      text-align: center;
      .top-header__contact {
        margin-top: 10px;
      }
    }
  }
  .navbar-brand {
    max-width: 191px;
    img {
      width: 100%;
    }
  }
  .navbar {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    padding-bottom: 20px;
    z-index: 100;
  }
  .header__nav {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    justify-content: flex-end;
    align-self: flex-end;
    @media only screen and (max-width: 1050px) {
      flex-direction: column;
      align-items: flex-end;
      &>* {
        margin-bottom: 5px;
      }
    }
  }
  .nav-item {
    margin-top: 10px;
    margin-left: 40px;
    &.active {
      border-bottom: 5px solid $cerulean;
      color: $cerulean;
    }
  }
  .nav-link {
    padding: 0;
    font-family: $futura;
    @include rfs(21);
    line-height: 0.8;
    white-space: nowrap;
    color: $slate-grey;
    border-bottom: 5px solid transparent;
    transition: all 0.2s ease;
    &:hover {
      color: $cerulean;
    }
  }
}

.wrapper {
  position: relative;
  overflow-x: hidden;
}

// SLIDER
.slider {
  background-color: $cerulean;
  overflow: hidden;
  .hero-slider {
    position: relative;
    margin-bottom: 0;
    .slick-dots {
      max-width: 1200px;
      position: absolute;
      bottom: 5.5vw;
      left: 0;
      right: 0;
      margin: auto;
      @media only screen and (max-width: 768px) {
        bottom: 0;
        text-align: center;
      }
      li {
        width: 16px;
        height: 16px;
        background-color: $white;
        border: solid 5px $white;
        border-radius: 50%;
      }
      li.slick-active {
        background-color: $cerulean;
      }
      button {
        opacity: 0;
      }
    }
  }
  .hero-slider__item {
    height: calc(100vh - 120px);
    position: relative;
    display: flex;
    justify-content: center;
    figure {
      padding: 0;
      margin: 0;
      text-align: center;
      min-width: 100%;
    }
    img {
      display: block;
      margin: 0 auto;
      min-width: 100%;
      min-height: 100%;
      @media only screen and (max-width: 1366px) {
        min-height: auto;
        width: 100%;
      }
    }
    @media only screen and (max-width: 1366px) {
      height: auto;
    }
  }
  .hero-slider__text {
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: absolute;
    top: 0;
    bottom: 12vw;
    left: 0;
    right: 0;
    margin: auto;
    @include rfs(16);
    color: $white;
    @media only screen and (max-width: 768px) {
      bottom: 5vw;
    }
  }
  .hero-slider__subtitle {
    font-family: $eurostile;
    @include rfs(29);
    letter-spacing: 2.9px;
    text-transform: uppercase;
  }
  .hero-slider__title {
    margin-top: 18px;
    margin-bottom: 26px;
    font-family: $futura;
    @include rfs(67);
    font-weight: 700;
  }
  .hero-slider__link {
    display: inline-block;
  }
}

.dark-bg {
  padding-top: 85px;
  padding-bottom: 65px;
  color: $white;
  background-color: #004464;
}

.notfall {
  margin-bottom: 61px;
  font-family: $eurostile;
  @include rfs(32);
  line-height: 1.37;
  text-align: center;
}

.btn__transparent {
  display: block;
  margin: 0 auto;
  padding: 0.48em 1.615em;
  font-family: $eurostile;
  @include rfs(23);
  letter-spacing: 2.3px;
  color: $white;
  border-radius: 30px;
  background-color: transparent;
  border: solid 2px $white;
  &:hover {
    background-color: $cerulean;
  }
}

.light {
  padding-top: 61px;
  padding-bottom: 50px;
  .btn__transparent {
    color: $cerulean-three;
    border: solid 2px $cerulean;
    &:hover {
      color: $white;
    }
  }
}

.light--small-pt {
  padding-top: 40px;
}

.content__subtitle {
  margin-bottom: 12px;
  font-family: $eurostile;
  @include rfs(29);
  letter-spacing: 2.9px;
  color: $slate-grey;
  text-align: center;
}

.content__title {
  margin-bottom: 50px;
  font-family: $futura;
  @include rfs(67);
  font-weight: 900;
  color: $cerulean-two;
  text-align: center;
}

.card__block {
  margin-bottom: 37px;
  font-family: $eurostile;
  @include rfs(23);
  color: $slate-grey-two;
  text-align: center;
  line-height: 1.26;
}

.card__icon-circle {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 164px;
  height: 164px;
  border: solid 2px $slate-grey;
  border-radius: 50%;
  img {
    min-width: 50px;
    max-width: 100%;
  }
}

.card__caption {
  margin-top: 28px;
  .bold-text {
    font-family: $futura;
    @include rfs(20);
    letter-spacing: 0.4px;
  }
}

.side-button-wrap {
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  right: -65px;
  z-index: 10;
  .side-button {
    display: flex;
    padding: 0.8em 1.7em 0.7em 1em;
    font-family: $eurostile;
    @include rfs(20);
    font-weight: 900;
    letter-spacing: 2px;
    line-height: 1;
    text-transform: uppercase;
    background-color: $pinkish-orange;
    box-shadow: 0 0 13px 0 #5b6c72;
    border: solid 2px $white;
    transform: rotate(-90deg);
    &:hover {
      background-color: darken( $pinkish-orange, 3%);
    }
  }
  .side-button__plus {
    width: 16px;
    height: 16px;
    margin-right: 10px;
  }
  span {
    color: $white;
  }
}

// PAGE: CONTENT
.content-head {
  -webkit-box-shadow: inset 0px 5px 13px 0px rgba(91, 108, 114, 0.2);
  -moz-box-shadow: inset 0px 5px 13px 0px rgba(91, 108, 114, 0.2);
  box-shadow: inset 0px 5px 13px 0px rgba(91, 108, 114, 0.2);
  background-color: #cedde2;
  .content-head__inner {
    padding-top: 50px;
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .content-head__nav {
    display: flex;
    @media only screen and (max-width: 768px) {
      flex-direction: column;
      align-items: center;
    }
  }
  .content-head__nav-link {
    margin-right: 50px;
    font-family: $futura;
    @include rfs(23);
    line-height: 1.26;
    letter-spacing: 0.5px;
    color: $cerulean;
    @media only screen and (max-width: 768px) {
      margin-right: 10px;
    }
    &:hover {
      color: $white;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}

// Video
.video-hero {
  min-height: 487px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: solid 1px $white;
  background-image: url("../img/content/video_bg.jpg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.video__play-button {
  max-width: 300px;
  display: flex;
  padding: 0.8em 2em;
  .play-button {
    width: 16px;
    height: 20px;
    margin-right: 15px;
  }
}

#modal__video-hero {
  margin: 0 auto;
  max-width: 992px;
}

// paw icon
.icon-paw {
  display: inline-block;
  margin-bottom: 15px;
  width: 23px;
  height: 24px;
}

.citation {
  padding-top: 66px;
  color: $white;
  text-align: center;
  font-family: $futura;
}

.citation__title {
  display: inline-block;
  padding: 0 35px 20px;
  margin-bottom: 20px;
  @include rfs(18);
  font-weight: 900;
  letter-spacing: 4.1px;
  border-bottom: 1px solid $white;
}

.citation__text {
  margin-bottom: 30px;
  font-family: $eurostile;
  @include rfs(32);
  line-height: 1.14;
  letter-spacing: 0.3px;
  text-align: center;
}

.citation__author {
  @include rfs(18);
  line-height: 1.31;
  letter-spacing: 4.1px;
}

// Facts
.facts {
  background-color: #0099d6;
  background-image: url("../img/clean_textile_blue.jpg");
  background-repeat: repeat;
  padding-top: 60px;
  padding-bottom: 76px;
  .facts__picture {
    width: 100%;
    margin-bottom: 26px;
  }
}

// History
.history {
  padding-top: 60px;
  padding-bottom: 70px;
  .container,
  .raw {
    max-width: 1366px;
  }
  .history__head {
    text-align: center;
  }
  .history__img {
    width: 100%;
  }
  .history__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: $eurostile;
    @include rfs(22);
    line-height: 1.37;
    letter-spacing: 0.2px;
    color: $slate-grey;
    @media only screen and (max-width: 992px) {
      margin-top: 30px;
    }
  }
  .history__title {
    font-size: inherit;
    line-height: 1.25;
    letter-spacing: 3.3px;
    text-transform: uppercase;
  }
}

// unser
.unser {
  padding-bottom: 50px;
  .unser__head {
    text-align: center;
  }
  .unser__foto-box {
    margin-top: 70px;
  }
  .unser__foto-box .row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .unser__foto {
    margin: 0 15px 32px 15px;
    width: 255px;
    height: 255px;
    background-color: $cerulean;
  }
}

// Citation-section
.citation-section {
  padding-bottom: 50px;
  background-color: $cerulean;
  background-image: url("../img/clean_textile_dark.jpg");
  .citation {
    max-width: 759px;
    margin: 0 auto;
  }
}

// Outpatient
.outpatient {
  padding-bottom: 146px;
  text-align: center;
  .outpatient__title {
    margin-bottom: 30px;
    font-size: inherit;
    line-height: 1.2;
    letter-spacing: 3.3px;
    text-transform: uppercase;
  }
  .outpatient__text {
    max-width: 966px;
    margin: 0 auto;
    font-family: $eurostile;
    @include rfs(21);
    line-height: 1.17;
    letter-spacing: 0.2px;
    color: $slate-grey;
  }
  .outpatient__paragraph {
    margin-bottom: 30px;
  }
  .outpatient__images-box {
    display: flex;
    justify-content: center;
    margin-top: 87px;
  }
  .outpatient__image-wrap {
    margin: 0px 15px 30px 15px;
    max-width: 446px;
  }
  .outpatient__image {
    width: 100%;
  }
}


/********************
***** TEAM PAGE *****
********************/

.team {
  .team__section {
    padding-top: 71px;
    padding-bottom: 51px;
  }
  .gallery__item {
    overflow: hidden;
  }
  .team__gallery {
    display: flex;
    align-items: stretch;
  }
  .gallery__photo {
    background-color: $white;
    border-top-right-radius: 40px;
    border-top-left-radius: 40px;
    img {
      border-top-right-radius: 40px;
      border-top-left-radius: 40px;
      width: 100%;
    }
  }
  .gallery__text {
    padding: 0 20px 0 25px;
    @include rfs(22);
    font-family: $eurostile;
    color: $slate-grey;
    background-color: $white;
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
  }
  .gallery__position {
    padding-top: 25px;
    font-family: $futura;
    @include rfs(15);
    font-weight: 900;
    text-transform: uppercase;
    line-height: 1.93;
    letter-spacing: 1.8px;
    color: #039de0;
  }
  .gallery__name {
    @include rfs(29.3);
    letter-spacing: 2.9px;
  }
  .gallery__desc {
    letter-spacing: 0.2px;
  }
  .gallery__link {
    display: block;
    padding: 0;
    margin-top: 25px;
    color: #039de0;
    background-color: transparent;
    border: none;
    &:hover {
      color: darken(#039de0, 5%)
    }
  }
  // interaction
  .gallery__text .hidden {
    display: none;
  }
  .open {
    .dots {
      display: none;
    }
  }
  // gallery with 3 cards layout
  .team__section--md {
    .gallery__item {
      margin-bottom: 23px;
      display: flex;
      flex-direction: column;
      border-top-right-radius: 30px;
      border-top-left-radius: 30px;
    }
    .gallery__position {
      padding-top: 10px;
    }
    .gallery__name {
      margin-bottom: 20px;
    }
    .gallery__text {
      padding: 0 15px 35px 15px;
    }
    .gallery__desc {
      @include rfs(19);
      line-height: 1.3;
    }
  }
  // gallery with 34 cards layout
  .team__section--sm {
    padding-top: 30px;
    .gallery__text {
      flex-grow: 1;
      padding-bottom: 20px;
    }
    .gallery__position {
      padding-top: 25px;
      padding-bottom: 5px;
    }
    .gallery__name {
      margin-bottom: 15px;
    }
  }
}


/********************
***** DOS PAGE *****
********************/

.dos {
  .content-head__inner {
    padding-bottom: 0px;
  }
  .dos__desc {
    padding-top: 78px;
    padding-bottom: 101px;
    line-height: 1.55;
    letter-spacing: 0.2px;
    @include rfs(22);
    font-family: $eurostile;
    color: $slate-grey;
  }
  .dos__desc-inner {
    margin: 0 auto;
  }
  .desc__title {
    margin-bottom: 30px;
    @include rfs(22);
    font-family: $eurostile;
    text-align: center;
    font-weight: 900;
  }
  .desc__list {
    margin-bottom: 40px;
  }
  .desc__list-item {
    .disc {
      display: inline-block;
      margin-right: 20px;
    }
  }
  // dos logos tiles
  .dos__partners {
    padding-top: 125px;
  }
  .partners__logo-box {
    max-width: 1366px;
    margin: 0 auto;
    padding-bottom: 107px;
    display: flex;
    flex-wrap: wrap;
  }
  .partners__item {
    flex-basis: 20%;
    min-height: 275px;
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    color: $slate-grey;
    border: dotted 1px #cedde2;
    @media only screen and (max-width: 992px) {
      flex-basis: 33.33%;
    }
    @media only screen and (max-width: 768px) {
      flex-basis: 50%;
    }
    @media only screen and (max-width: 360px) {
      flex-basis: 100%;
    }
  }
  .logo__title {
    @include rfs(33);
    margin-bottom: 25px;
  }
  .partners__name {
    margin-bottom: 80px;
    font-family: $eurostile;
    @include rfs(23);
    letter-spacing: 0.2px;
  }
}


/********************
***** KLINIK PAGE *****
********************/

.klinik {
  .klinik-section {
    padding-top: 130px;
    padding-bottom: 130px;
  }
  .klinik__paragraph {
    margin-bottom: 2rem;
    text-align: justify;
    font-family: $eurostile;
    @include rfs(22);
    line-height: 1.55;
    letter-spacing: 0.2px;
    text-align: left;
    color: $slate-grey;
  }
  // table with icons
  .table {
    .table__row {
      border-top: 4px solid rgba(0, 0, 0, 0.1);
      padding-top: 40px;
      padding-bottom: 40px;
      font-family: $eurostile;
      &:last-child {
        border-bottom: 4px solid rgba(0, 0, 0, 0.1);
      }
    }
    .table__icons-wrap {
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
    .toggler {
      position: relative;
      width: 26px;
      height: 26px;
      border: none;
      background-color: transparent;
      &:hover .icon {
        background-color: darken($cerulean-three, 5%);
      }
      .icon {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        display: inline-block;
        background-color: $cerulean-three;
      }
      .icon-minus {
        width: 26px;
        height: 4px;
      }
      .icon-plus {
        width: 4px;
        height: 26px;
        transition: all 0.3s ease;
      }
    }
    .open .icon-plus {
      height: 0;
    }
    .table__icon {
      img {
        max-width: 71px;
      }
    }
    .table__text {
      display: flex;
      flex-direction: column;
      .klinik__paragraph {
        display: none;
      }
    }
    .table__row-title {
      color: $cerulean-two;
    }
  }
  // slider section
  .klinik__img-box {
    img {
      width: 100%;
    }
  }
}

.slider-section {
  margin-top: 68px;
  background-color: rgba(91, 108, 114, 0.11);
}

.klinik__slider {
  .klinik__slide {
    padding-top: 3.5%;
    padding-right: 2%;
    @media only screen and (min-width: 1366px) {
      padding-top: 148px;
    }
    @media only screen and (max-width: 1366px) {
      padding-top: 2%;
      padding-right: 1%;
    }
    @media only screen and (max-width: 1200px) {
      padding-top: 100px;
      padding-right: 0;
    }
    @media only screen and (max-width: 575px) {
      padding-left: 0.5%;
      padding-right: 0.5%;
    }
  }
  .klinik__img-box {}
  .content__subtitle {
    text-align: left;
    margin-bottom: 20px;
  }
  .slick-prev,
  .slick-next {
    top: 100px;
    left: 0;
    display: block;
    padding-left: 3px;
    width: 31px;
    height: 50px;
    background-color: $white;
    z-index: 1000;
    @media only screen and (max-width: 1366px) {
      top: 50px;
    }
    @media only screen and (max-width: 575px) {
      left: 4%;
    }
    &:before {
      content: "";
      display: block;
      width: 17px;
      height: 22px;
      font-family: "slick";
      font-size: 20px;
      line-height: 22px;
      color: $slate-grey;
      background-size: cover;
      opacity: 0.75;
    }
  }
