// Footer
.footer {
  @include rfs(21);
  color: $blue-grey;
  background-color: $slate-blue;
  background-image: url("../img/clean_textile_dark.jpg");
  a {
    color: inherit;
    font-size: inherit;
    &:hover {
      color: $cerulean;
    }
  }
  .footer__inner {
    display: flex;
    justify-content: space-between;
    @media only screen and (max-width: 640px) {
      flex-direction: column;
      align-items: center;
    }
  }
  // footer top-nav
  .footer__top-nav {
    padding-top: 70px;
    padding-bottom: 80px;
    color: $slate-grey;
    background-color: #cedde2;
    background-image: url('../img/clean_textile_light.jpg');
  }
  .top-nav__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @media only screen and (max-width: 480px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align-last: center;
    }
  }
  .top-nav__list {
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 0;
    @media only screen and (max-width: 480px) {
      padding-left: 0;
      padding-right: 0;
    }
    @media only screen and (max-width: 867px) {
      margin-bottom: 30px;
    }
  }
  .top-nav__title {
    @include rfs(32);
    line-height: 0.8;
    white-space: nowrap;
    color: $cerulean;
  }
  .top-nav__column--last {
    align-self: flex-end;
    padding-bottom: 6px;
    @media only screen and (max-width: 480px) {
      align-self: center;
    }
    @media only screen and (max-width: 867px) {
      margin-bottom: 30px;
    }
  }
  .top-nav__logo {
    max-width: 191px;
    margin-left: -10px;
    margin-bottom: 16px;
  }
  .top-nav__tel,
  .top-nav__schedule {
    margin-bottom: 0;
    @include rfs(17);
    line-height: 1.23;
    span:first-child {
      font-weight: 700;
    }
  }
  .footer__bottom-inner {
    justify-content: space-between;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  // footer bottom-nav
  .footer__bottom-nav {
    display: flex;
  }
  .bottom-nav__link {
    margin-right: 25px;
    &:hover {
      color: $white;
    }
  }
  .footer__copy {
    font-family: $futura;
    letter-spacing: 0.2px;
    text-align: right;
  }
}